<template>
  <div class="todo-footer" v-show="total">
          <label>
            <input type="checkbox" :checked="isAll" @change="checkAll" />
            <!-- 这里的:checked 和 @change也可以写成一个v-model="isAll" 
            且要用computed的完整写法设置setter修改MyList中input的checked属性 -->
          </label>
          <span>
            <span>已完成{{doneTotal}}</span> / 全部{{total}}
          </span>
          <button class="btn btn-danger" @click="clearAll">清除已完成任务</button>
        </div>
</template>

<script>
export default {
name: 'FooterList',
props: ['todos'],
computed:{
    doneTotal(){
        // return this.todos.filter(todo => todo.completed).length
        return this.todos.reduce((total,todo) => {
            return todo.completed ? total + 1 : total
        },0)
    },
    total(){
        return this.todos.length
    },
    isAll(){
        return this.todos.every(todo => todo.completed) && this.todos.length > 0
    },
    
},
methods:{
    // 全选按钮的点击事件
    checkAll(value){
        // this.checkAllTodo(e.target.checked)
        this.$emit('checkAllTodo',value.target.checked)
    },
    // 清除已完成任务按钮的点击事件
    clearAll(){
        // this.clearAllTodo()
        this.$emit('clearAllTodo')
    }
}
}
</script>

<style scoped>
/*footer*/
.todo-footer {
  height: 40px;
  line-height: 40px;
  padding-left: 6px;
  margin-top: 5px;
}

.todo-footer label {
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}

.todo-footer label input {
  position: relative;
  top: -1px;
  vertical-align: middle;
  margin-right: 5px;
}

.todo-footer button {
  float: right;
  margin-top: 5px;
}

</style>