<template>
        <li>
            <label>
                <!-- 虽然可以直接将:checked 和 @click事件直接写成v-model="centent.completed" 省去了很多工作
                但由于实际上对props中的内容进行了修改因此不建议使用 -->
              <input type="checkbox" :checked="content.completed" @click="handleCheck(content.id)"/>  
              
              <span v-show="!content.isEdit">{{content.title}}</span>
              <input 
              v-show="content.isEdit" 
              type="text" 
              :value="content.title" 
              @blur="handleBlur(content,$event)"
              ref="inputTitle"/>
            </label>
            <button class="btn btn-danger" @click="handleDelete(content.id)">删除</button>
            <button v-show="!content.isEdit" class="btn btn-edit" @click="handleEdit(content)">编辑</button>

        </li>
</template>

<script>
import pubsub from 'pubsub-js'
export default {
    name: 'MyList',
    //声明接收父组件的属性
    props: ['content'],
    methods:{
        handleCheck(id){
            // this.checkTodo(id)
            this.x.$emit('checkTodo',id)
        },
        handleDelete(id){
            if(confirm('确定删除吗？')){
                // this.deleteTodo(id)
                // this.x.$emit('deleteTodo',id)
                pubsub.publish('deleteTodo',id)
            }
           
        },
         handleEdit(content){
              if (content.hasOwnProperty('isEdit')) {
                content.isEdit = true}
                else{this.$set(content,'isEdit',true)}
                // content.isEdit = false 若直接添加此句话则会导致页面响应式的问题需要用$set
              //setTimeout(()=>{
                //this.$refs.inputTitle.focus()
                /* 直接写由于vue要等函数代码全执行后才重新解析模板，到这里时input还在隐藏
                保证了模板解析的效率 所以要等到下一次DOM节点更新结束后在执行*/
              //},0)
              //使用vue提供的api $nextTick()也可以做到
              this.$nextTick(function(){
                this.$refs.inputTitle.focus()
              })
              
            },
          handleBlur(content,e){
            content.isEdit = false
            //失去焦点时将新的值赋给title
            this.x.$emit('updateTodo',content.id,e.target.value)
          }
    },
    
   
}

</script>

<style scoped>
/*item*/
li {
  list-style: none;
  height: 36px;
  line-height: 36px;
  padding: 0 5px;
  border-bottom: 1px solid #ddd;
}

li label {
  float: left;
  cursor: pointer;
}

li label li input {
  vertical-align: middle;
  margin-right: 6px;
  position: relative;
  top: -1px;
}

li button {
  float: right;
  display: none;
  margin-top: 3px;
}

li:before {
  content: initial;
}

li:last-child {
  border-bottom: none;
}

li:hover{
    background-color: #f5f5f5;
}

li:hover button{
    display: block;
}

</style>