<template>
  <div id="root">
    <div class="todo-container">
      <div class="todo-wrap">
        <!-- 向子组件中传递函数或数据 -->
        <HeaderInput @addTodo="addTodo"/>
        <MainList :todos="todos" />
        <Footer-list :todos="todos" @checkAllTodo="checkAllTodo" @clearAllTodo="clearAllTodo"/>
      </div>
    </div>
  </div>

</template>

<script>
import pubsub from 'pubsub-js'
import HeaderInput from './components/HeaderInput.vue'
import MainList from './components/MainList.vue'
import FooterList from './components/FooterList.vue'

export default {
  name: 'App',
  data(){
return {
        todos:JSON.parse(localStorage.getItem('todos')) || [],// 放入localStorage中的数据
        }
  },
  components: {
    HeaderInput,
    MainList,
    FooterList,
  },
  methods: {
    //input 添加 todo 
    addTodo(todo) {
          this.todos.unshift(todo)

        },
    //勾选数据更新
    checkTodo(id){
      this.todos.forEach(todo => {
        if(todo.id === id){
          todo.completed = !todo.completed
        }
      })
    },
    //删除todo
    deleteTodo(_,id){
      this.todos = this.todos.filter((todo) => todo.id !== id)
    },
    //全选或全不选
    checkAllTodo(done){
      this.todos.forEach(todo => {
        todo.completed = done
      })
    },
    //清除已完成todo
    clearAllTodo(){
      this.todos = this.todos.filter(todo => !todo.completed)

    },
    updateTodo(id,title){
      if(title.trim()){
        this.todos.forEach(todo => {
          if(todo.id === id){
            todo.title = title.trim()
          }
        })
      }else{
        alert("内容不能为空")
      }
    }
  },
  watch:{
    todos:{
      deep:true,
      handler(value){
      localStorage.setItem('todos',JSON.stringify(value))//监视todos数据变化，更新localStorage
    }
  }},
  mounted(){
        this.x.$on('checkTodo',this.checkTodo)
        // this.x.$on('deleteTodo',this.deleteTodo)
        this.pubId= pubsub.subscribe('deleteTodo',this.deleteTodo)
        this.x.$on('updateTodo',this.updateTodo)
    },
  beforeDestroy(){
        this.x.$off('checkTodo',this.checkTodo)
        // this.x.$off('deleteTodo',this.deleteTodo)
        pubsub.unsubscribe('deleteTodo',this.pubId)
    }
}
</script>

